.listWithActionsv2 ul li {
    cursor: pointer;
    background-color: var(--list-item-background);
    display: flex;
    align-items: center;
    margin-bottom: 12px;
    padding: 22px 24px;
    transition: .2s;
    border-radius: var(--radius);
}
.fund__move-in, .fund__move-out {
   
    flex-direction: column;
    display: inline-flex;
}
.fund__move-in {
    margin-right: 4%;
}
.listWithActionsv2 ul li .details {
    margin-right: auto;
    flex-direction: column;
}

.fund .fund__move-in .listWithActionsv2 ul li .details .label, .fund .fund__move-out .listWithActionsv2 ul li .details .label {
    font-size: 20px;
    padding-bottom: 6px;
}